import React, { useState, useEffect } from 'react';
import { QRCodeCanvas } from 'qrcode.react';
import { QrReader } from 'react-qr-reader';

const AlarmComponent = () => {
  const [alarmTime, setAlarmTime] = useState('');
  const [currentTime, setCurrentTime] = useState(new Date());
  const [isAlarmTriggered, setIsAlarmTriggered] = useState(false);
  const [qrCodeData, setQRCodeData] = useState('');
  const [isScanning, setIsScanning] = useState(false);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    if (alarmTime) {
      const [hours, minutes] = alarmTime.split(':');
      const alarmDate = new Date();
      alarmDate.setHours(parseInt(hours, 10));
      alarmDate.setMinutes(parseInt(minutes, 10));
      alarmDate.setSeconds(0);

      if (
        currentTime.getHours() === alarmDate.getHours() &&
        currentTime.getMinutes() === alarmDate.getMinutes() &&
        currentTime.getSeconds() === 0
      ) {
        setIsAlarmTriggered(true);
      }
    }
  }, [alarmTime, currentTime]);

  const handleAlarmSet = (e) => {
    setAlarmTime(e.target.value);
    setIsAlarmTriggered(false);
    generateQRCode();
  };

  const generateQRCode = () => {
    const randomString = Math.random().toString(36).substring(2, 15);
    setQRCodeData(`ALARM_${randomString}`);
  };

  const handleScan = (result, error) => {
    if (!!result) {
      if (result?.text === qrCodeData) {
        setIsAlarmTriggered(false);
        setIsScanning(false);
        alert('');
      } else {
        alert('QR');
      }
    }

    if (!!error) {
      console.error('QR:', error);
    }
  };

  return (
    <div>
      <h1></h1>
      <p>: {currentTime.toLocaleTimeString()}</p>
      <input
        type="time"
        value={alarmTime}
        onChange={handleAlarmSet}
      />
      <p>: {alarmTime}</p>
      {isAlarmTriggered && (
        <div>
          <h2></h2>
          <button onClick={() => setIsScanning(true)}>QR</button>
        </div>
      )}
      
      {qrCodeData && (
        <div>
          <h3>QR:</h3>
          <QRCodeCanvas value={qrCodeData} />
          <p>QR: {qrCodeData}</p>
        </div>
      )}

      {isScanning && (
        <div>
          <h3>QR</h3>
          <QrReader
            constraints={{
                facingMode: 'environment'
            }}
            onResult={handleScan}
            style={{ width: '100%' }}
          />
        </div>
      )}
    </div>
  );
};

export default AlarmComponent;